.header {
  display: flex;
  justify-content: center;
  height: 60px;

  &__container {
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 1200px;
    padding: 0 20px;
  }

  &__logo {
    width: 119px;
    height: 29px;
    background: url("../images/logo.png") no-repeat;
    background-size: 119px 29px;
  }

  &__nav {
    display: flex;
    align-items: center;
    margin: 0 0 0 auto;
    padding: 0;
    list-style: none;
  }

  &__nav-item {
    margin: 0 18px;
  }

  &__nav-link {
    font-family: $font-family;
    font-weight: 400;
    font-size: 14px;
    color: #333333;
    text-decoration: none;
    transition: color 0.1s ease-in-out;

    &_bl {
      font-weight: 600;
      color: #3b99d3;
    }

    &:hover {
      color: #000000;
    }

  }

  &__user {
    display: inline-block;
    position: relative;
    padding-left: 20px;
    margin-left: 23px;
    text-decoration: none;

    &:before {
      position: absolute;
      content: "";
      left: 0;
      top: 10px;
      width: 10px;
      height: 10px;
      background: url(data:image/svg+xml;utf8;base64,) no-repeat;
      background-size: 10px 10px;
    }

  }

  &__user-avatar {
    vertical-align: middle;
    border-radius: 50%;
    width: 29px;
    height: 29px;
  }

}
