.card {
  background: #fff;

  &__img {
    height: 396px;
    background-size: cover;
    background-position: center center;
    width: 100%;
  }

  &__wrap {
    padding: 0 30px 30px;
  }

  &__title {
    margin: 0;
    padding: 36px 0 42px 0;
    font-family: $font-family;
    font-weight: 800;
    font-size: 48px;
    color: #333333;
    line-height: normal;
  }

  &__divider {
    border-top: 1px solid #e6e6e6;
    margin-bottom: 25px;
  }

  &__date {
    position: relative;
    margin: 0;
    padding: 0 0 15px 23px;
    font-family: $font-family;
    font-weight: 400;
    font-size: 15px;
    color: #666;

    &:before {
      position: absolute;
      content: "";
      left: 0;
      top: 0;
      width: 15px;
      height: 15px;
      background: url(data:image/svg+xml;utf8;base64,) no-repeat;
      background-size: 15px 15px;
    }

  }

  &__location {
    position: relative;
    margin: 0;
    padding: 0 0 15px 23px;
    font-family: $font-family;
    font-weight: 400;
    font-size: 15px;
    color: #666;

    &:before {
      position: absolute;
      content: "";
      left: -3px;
      top: 0;
      width: 20px;
      height: 20px;
      background: url(data:image/svg+xml;utf8;base64,) no-repeat;
      background-size: 20px 20px;
    }

  }

  &__tags {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    list-style: none;
    line-height: 30px;
  }

  &__tags-item {
    margin-right: 5px;
  }

  &__tags-link {
    padding: 0 15px 1px 15px;
    border: 1px solid #dc8b2b;
    border-radius: 15px;
    font-family: $font-family;
    font-weight: 400;
    font-size: 14px;
    color: #dc8b2b;
    text-decoration: none;
    transition: background 0.1s ease-in-out, color 0.1s ease-in-out;

    &:hover {
      color: #fff;
      background: #dc8b2b;
    }

    &:active {
      color: #fff;
      background: #c67d27;
    }

  }

  &__btn-box {
    padding: 0 7px;
    display: inline-block;
    text-align: center;
  }

  &__btn {
    width: 165px;
    padding: 12px;
    background: #3b99d3;
    border-radius: 25px;
    text-align: center;
    font-family: $font-family;
    font-weight: 600;
    font-size: 17px;
    color: #fff;
    border: 0;
    outline: 0;
    cursor: pointer;
    box-shadow: 0 7px 15px 0 rgba(59, 153, 211, 0.25);
    transition: box-shadow 0.1s ease-in-out, background 0.1s ease-in-out;

    &:hover {
      background: #3790c6;
    }

    &:active {
      background: #3283b4;
      box-shadow: none;
    }

  }

  &__detail {
    font-family: $font-family;
    font-weight: 400;
    font-size: 14px;
    color: #666;

    &_y {
      color: #f39c12;
    }

  }

  &__btn-promote {
    position: relative;
    width: 165px;
    padding: 12px;
    background: #f7f7f7;
    border-radius: 25px;
    text-align: center;
    font-family: $font-family;
    font-weight: 600;
    font-size: 17px;
    color: #f39c12;
    border: 0;
    outline: 0;
    cursor: pointer;
    transition: background 0.1s ease-in-out;

    &_mt {
      margin-top: 17px;
    }

    &:hover {
      background: #eeeeee;
    }

    &:active {
      background: #e6e6e6;
    }

    &:after {
      position: absolute;
      content: "";
      right: 8px;
      top: 8px;
      width: 27px;
      height: 27px;
      background: url('data:image/png;base64,') no-repeat;
      background-size: 27px 27px;
    }

  }

  &__btn-promote-tooltip {
    height: 0;
    opacity: 0;
    overflow: hidden;
    transform: scale(.8);
    transition: all .1s ease-out;

    position: absolute;
    left: -17px;
    top: 60px;
    padding: 0;
    width: 200px;
    color: #fff;
    background: #f39c12;
    border-radius: 5px;
    box-shadow: 0 7px 15px 0 rgba(243, 156, 18, 0.25);

    font-weight: 400;
    font-size: 15px;
    text-align: left;
    line-height: normal;

    z-index: 2;

    &:before {
      position: absolute;
      content: "";
      left: 90px;
      top: -15px;
      width: 25px;
      height: 25px;
      background: url(data:image/svg+xml;utf8;base64,) no-repeat;
      background-size: 25px 25px;
    }

  }

  &__btn-promote-tooltip-price {
    display: block;
    padding-top: 20px;
    font-weight: 800;
    font-size: 24px;
  }

  &__btn-promote:hover > &__btn-promote-tooltip {
    padding: 15px;
    transform: scale(1);
    overflow: visible;
    opacity: 1;
    height: auto;
    z-index: 10;
  }

}

.card-detail {
  margin-top: 30px;
  padding: 10px 30px 10px;
  background: #fff;
  border-radius: 5px;
  font-family: $font-family;

  & h2 {
    font-weight: 800;
    font-size: 24px;
    color: #333333;
    line-height: normal;
  }

  & p {
    font-weight: 300;
    font-size: 15px;
    color: #666666;
    line-height: 30px;
  }

}

.card-side {
  max-width: 326px;
  margin: 30px 30px 0;
  padding: 15px;
  background: #fff;
  border-radius: 5px;

  &_mt {
    margin-top: 10px;
  }

  &_ptb {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  &_pb {
    padding-bottom: 25px;
  }

  &__user {
    text-decoration: none;
  }

  &__user-avatar {
    vertical-align: middle;
    border-radius: 50%;
    width: 29px;
    height: 29px;
  }

  &__user-name {
    padding-left: 15px;
    font-family: $font-family;
    font-weight: 400;
    font-size: 14px;
    color: #3399cc;
    transition: color 0.1s ease-in-out;

    &:hover {
      color: #2d87b4;
    }

    &_verified {
      position: relative;

      &:after {
        position: absolute;
        content: "";
        right: - 20px;
        top: 2px;
        width: 14px;
        height: 14px;
        background: url(data:image/svg+xml;utf8;base64,) no-repeat;
        background-size: 14px 14px;
      }

    }

  }

  &__title {
    margin: 0;
    padding: 10px 45px;
    font-family: $font-family;
    font-weight: 800;
    font-size: 23px;
    color: #333333;
    line-height: normal;
  }

  &__date {
    position: relative;
    margin: 0 45px;
    padding: 0 0 15px 23px;
    font-family: $font-family;
    font-weight: 400;
    font-size: 14px;
    color: #666;

    &:before {
      position: absolute;
      content: "";
      left: 0;
      top: 0;
      width: 13px;
      height: 13px;
      background: url(data:image/svg+xml;utf8;base64,) no-repeat;
      background-size: 13px 13px;
    }

  }

  &__location {
    position: relative;
    margin: 0 45px;
    padding: 0 0 15px 23px;
    font-family: $font-family;
    font-weight: 400;
    font-size: 14px;
    color: #666;

    &:before {
      position: absolute;
      content: "";
      left: -3px;
      top: 0;
      width: 17px;
      height: 17px;
      background: url(data:image/svg+xml;utf8;base64,) no-repeat;
      background-size: 17px 17px;
    }

  }

  &__btn-box {
    padding: 20px 0;
    text-align: center;
  }

  &__btn-like {
    position: relative;
    padding-left: 33px;
    background: transparent;
    border: none;
    font-family: $font-family;
    font-weight: 400;
    font-size: 14px;
    color: #4c4c4c;
    outline: 0;
    cursor: pointer;
    transition: color 0.1s ease-in-out;

    &:hover {
      color: #4381E3;
    }

    &:after {
      position: absolute;
      content: "";
      left: 0;
      top: 0;
      width: 22px;
      height: 22px;
      background: url(data:image/svg+xml;utf8;base64,) no-repeat;
      background-size: 22px 22px;
    }

  }

  &__btn-share {
    position: relative;
    float: right;
    width: 22px;
    height: 22px;
    background: transparent;
    border: none;
    outline: 0;
    cursor: pointer;
    transition: color 0.1s ease-in-out;

    &:hover {
      color: #4381E3;
    }

    &:after {
      position: absolute;
      content: "";
      left: 0;
      top: 0;
      width: 22px;
      height: 22px;
      background: url(data:image/svg+xml;utf8;base64,) no-repeat;
      background-size: 22px 22px;
    }

  }

  &__users-go-link {
    text-decoration: none;
  }

  &__users-go {
    position: relative;
  }

  &__users-go-avatar {
    position: absolute;
    top: -3px;
    vertical-align: middle;
    border-radius: 50%;
    width: 26px;
    height: 26px;
    border: 2px solid #fff;

    &_1 {
      z-index: 2;
    }

    &_2 {
      left: 15px;
    }

  }

  &__users-go-text {
    padding-left: 50px;
    font-family: $font-family;
    font-weight: 400;
    font-size: 14px;
    color: #666;
    transition: color 0.1s ease-in-out;

    &:hover {
      color: #4381E3;
    }
  }

}

.card-side-fixed {
  display: none;
  position: fixed;
  top: 0;

  &_show {
    display: block;
  }

}

.card-side-fixed{
  animation: animationIn ease 0.5s;
  animation-iteration-count: 1;
  transform-origin: 50% 50%;
  animation-fill-mode:forwards; /*when the spec is finished*/
  -webkit-animation: animationIn ease 0.5s;
  -webkit-animation-iteration-count: 1;
  -webkit-transform-origin: 50% 50%;
  -webkit-animation-fill-mode:forwards; /*Chrome 16+, Safari 4+*/
  -moz-animation: animationIn ease 0.5s;
  -moz-animation-iteration-count: 1;
  -moz-transform-origin: 50% 50%;
  -moz-animation-fill-mode:forwards; /*FF 5+*/
  -o-animation: animationIn ease 0.5s;
  -o-animation-iteration-count: 1;
  -o-transform-origin: 50% 50%;
  -o-animation-fill-mode:forwards; /*Not implemented yet*/
  -ms-animation: animationIn ease 0.5s;
  -ms-animation-iteration-count: 1;
  -ms-transform-origin: 50% 50%;
  -ms-animation-fill-mode:forwards; /*IE 10+*/
}

@keyframes animationIn{
  0% {
    opacity:0;
    transform:  translate(0px,-60px)  ;
  }
  100% {
    opacity:1;
    transform:  translate(0px,0px)  ;
  }
  100% {
    opacity:1;
    transform:  translate(0px,0px)  ;
  }
}

@-moz-keyframes animationIn{
  0% {
    opacity:0;
    -moz-transform:  translate(0px,-60px)  ;
  }
  100% {
    opacity:1;
    -moz-transform:  translate(0px,0px)  ;
  }
  100% {
    opacity:1;
    -moz-transform:  translate(0px,0px)  ;
  }
}

@-webkit-keyframes animationIn {
  0% {
    opacity:0;
    -webkit-transform:  translate(0px,-60px)  ;
  }
  100% {
    opacity:1;
    -webkit-transform:  translate(0px,0px)  ;
  }
  100% {
    opacity:1;
    -webkit-transform:  translate(0px,0px)  ;
  }
}

@-o-keyframes animationIn {
  0% {
    opacity:0;
    -o-transform:  translate(0px,-60px)  ;
  }
  100% {
    opacity:1;
    -o-transform:  translate(0px,0px)  ;
  }
  100% {
    opacity:1;
    -o-transform:  translate(0px,0px)  ;
  }
}

@-ms-keyframes animationIn {
  0% {
    opacity:0;
    -ms-transform:  translate(0px,-60px)  ;
  }
  100% {
    opacity:1;
    -ms-transform:  translate(0px,0px)  ;
  }
  100% {
    opacity:1;
    -ms-transform:  translate(0px,0px)  ;
  }
}
