$font-family: 'Open Sans', sans-serif;

@import "normalize";

* {
  box-sizing: border-box;
}

body {
  display: flex;
  margin: 0;
}

.content-wrap {
  width: 100%;
  flex: 1 1 auto;
  display: flex;
  flex-flow: column nowrap;
  min-height: 100vh;
}

main {
  width: 100%;
  flex: 1 1 auto;
  background: #f7f7f7;
}

.container {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

@import "normalize";
@import "grid";
@import "header";
@import "card";
@import "footer";
@import "share";
@import "modal";

.text-align {

  &_r {
    text-align: right;
  }

}

